import cmdNavBar from "@/components/cmd-nav-bar/cmd-nav-bar.vue"
import http from '@/http/interface.js'
export default {
	components: {cmdNavBar},
	data() {
		return {
			imgArray : {
				add : require('../../../static/icon/m-zx-h.png')
			},
			isRecharged : 'N'
		};
	},
	onLoad() {
		
	},
	created() {
		let _this = this;
		http.get('transaction/totransaction',{phone:this.$store.state.userInfo.phone}).then((res)=>{
			console.log(res.data.data.balance.quota);
			if(Number(res.data.data.balance.quota) >= 2000){
				_this.isRecharged = 'Y'
			}
		})
	},
	computed:{
		storeData(){
			return this.$store.state.mainPoolData;
		}
		
	},
	methods: {
		//进入买入股票
		buyIn(stockCode){
			uni.navigateTo({
				url : '/pages/main/transaction/buy/buy?type=zxgp&stockCode='+stockCode
			})
		},
		//加入自选
		addMyStock(el){
			http.get('stock/addStock',{stockCode:el.stockCode,stockName:el.stockName,holder:this.$store.state.userInfo.phone}).then((res)=>{
				uni.showModal({
					title : '提示',
					content : '加入自选股票成功',
					showCancel : false,
				})
			})
		},
		goBuy(){
			uni.navigateTo({
				url : '/pages/main/myAccount/pay/pay'
			})
		},
		initData(){
			uni.showLoading({
				mask:true
			});
			http.get('transaction/toGoldStock').then((res)=>{
				this.$store.commit('mainPoolDataUpdate',res.data.data)
			})
		}
	},
	mounted() {
		this.initData();
	}
}
